.communique {
    margin: 30px auto;
}

.communique img {
    display: block;
    width: 100%;
}

.communiques {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}
.content {
    position: relative;
    /* width: 400px; */
    /* border-radius: 20px; */
    /* padding: 15px; */
}
.content img {
    border-radius: 10px;
}

.info {
    left: 40px;
    position: absolute;
    bottom: 20px;
}
.info h3 {
    font-size: 24px;
    color: orange;
    font-weight: bold;
}
.info p {
    font-size: 15px;
    color: #fff;
    font-weight: bold;
}
.info a {
   background: orange;
   color: blue;
}

.content .contentTwo {
    display: grid;
    grid-template-columns: 1fr 1fr;
    position: relative;
    gap: 10px;

}
.content .contentTwo .info {
    position: absolute;
    /* display: none; */
    
}


@media (max-width: 768px) { 
    .info p {
        display: none;
    }
}

@media (max-width: 576px) { 
    .info {
        display: none;
    }
}