.footer {
    background: blue;
    padding: 30px;
}
.footer a img {
   display: block;
   width: 120px;
}
.content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: orange;
}


.content a {
    color: orange;
    margin-bottom: 5px;
}
.content .itemsLeft a, .content .itemsCenter a, .content .itemsRight a {
    margin-bottom: 10px;
}

.content .itemsCenter a {
    display: block;
    border: 1px solid orange;
    padding: 20px 3px;
    text-align: center;
}
.content .itemsCenter a::before {
    content: '↑';
    display: block;
    margin-bottom: 5px;
    text-align: center;
    
}

@media (max-width: 768px) { 
    .content {
        display: grid;
        grid-template-columns: 1fr 1fr;

       
    }

    .content .itemsCenter a {
        display: block;
        border: 1px solid orange;
        padding: 15px 3px;
        text-align: center;
    }
}

