.logos {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.logos img {
    display: block;
    width: 100%;
}

@media (max-width: 768px) { 
    .logos {
        flex-direction: column;
        justify-content: center;
    }

    .logos a {
        margin-bottom: 10px;
    }
}
