.navbar {
    max-width: 1270px;
    padding: 1.35rem;
    display: grid;
    grid-template-columns: minmax(300px, 1fr) 2fr 1fr ;
    gap: 20px;
}

@media (max-width: 768px) { 
    .navbar {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 576px) { 
    .navbar {
        grid-template-columns: 1fr ;
    }
}

