.header {
    margin-bottom: 30px;
}

.header h3 {
    font-size: 28px;
    color: blue;
    font-weight: 600;
}
.header span {
    color: #777;
    font-size: 11px;
}

   

@media (max-width: 768px) { 
    .header h3 {
        font-size: 24px;
        font-weight: 400;
        text-align: center;
    }
    .header span {
        font-size: 11px;
        text-align: center;
        display: block;
    }

}

@media (max-width: 576px) { 
    .header h3 {
        font-size: 20px;
        text-align: center;
    }

    .header span {
        display: block;
        text-align: center;
    }
   
}