.card {
    border: 1px solid #ccc;
    border-radius: 10px;
    box-shadow: 1px 1px 1px #ccc;
    
}
.card img {
    display: block;
    width: 100%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom: 3px solid #ccc;

}
.cardInfo {
    padding: 20px;
}
.card h5 {
    color: blue;
    margin: 5px 0 15px 0;
}
.card p {
    font-size: 11px;
    color: #333;
}
.card a {
    margin-top: 30px;
}
