.leadMoreButton {
    display: inline-block;
    background: blue;
    color: orange;
    margin-top: 10px;
    padding: 4px 10px;
    font-size: 12px;
    border-radius: 5px;
    font-weight: bold;
}
.leadMoreButton:hover {
    background: rgb(27, 27, 141);
    color: orange;
    transition: .3s;
}