.cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 25px;
    margin: 30px auto;
}


@media (max-width: 768px) { 
    .cards {
        grid-template-columns: 1fr 1fr;
        gap: 10px;
    }
}

@media (max-width: 576px) { 
    .cards {
        grid-template-columns: 1fr;
        gap: 10px;
    }

}