.buttons {
    display: flex;
    align-items: center;
    gap: 20px;
}
.services {
    background: orange;
    color: blue;
    padding: 5px 10px;
    font-size: 13px;
    cursor: pointer;
}
.language {
    display: flex;
    border: 1px solid #ccc;
    background: #fff;
}
.language a {
    font-size: 13px;
    color: blue;
    padding: 4px;
    text-align: center;
}
.language a:first-child {
   width: 50px;
}
.language a:last-child {
   width: 65px;
}
.activeLanguage {
    background: blue;
    color: orange !important;
}

.buttons input {
    padding: 4px 10px;
    max-width: 140px;
}